.box__input {
	width: 400px;
	min-width: 330px;
	background-color: rgba(255, 255, 255, 0.12);
	border-radius: 10px;
	padding: 15px 20px;
	margin-bottom: 20px;
}
.box__title {
	font-style: normal;
	font-weight: 700;
	font-size: 17px;
	margin-bottom: 15px;
	letter-spacing: 0.06rem;
}
.mr-10 {
	margin-right: 10px;
}
@media screen and (max-width: 447px) {
	.mr-10 {
		margin-right: 0;
	}
}
